import { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import { Navbar, Nav, NavDropdown, Jumbotron, Container, Button, Spinner } from 'react-bootstrap';
import itemDatas from'./itemdata';
import { Link, Route, Switch } from 'react-router-dom';
import Detail from './Detail';
import axios from 'axios';

function App() {
  const [shoesData, setShoesData] = useState(itemDatas);
  const [shoeLoading, setShoeLoading] = useState(false);
  const [shoeStock, setShoeStock] = useState([10,11,12]);

  return (
    <div>
      <Navbar bg="dark" variant="dark" expand="lg">
        <Link className="navbar-brand" to="/">
          <img
            alt="logo"
            src={logo}
            width="36"
            height="36"
            className="d-inline-block align-top"
          />{' '}
          SHOP
        </Link>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      
      <Switch>
        <Route exact path="/">
          <Jumbotron fluid className="cus_bnr">
            <Container>
              <h1>영타기기여워잉</h1>
              <p>
              얼굴근육이 남다르게 자유분방해서 이 남자 얼굴에 질릴틈없이 덕질가능<br className="md_br" /> 표정도 다양하고 눈동자까지 때굴때굴 굴려가며 표현하니까 얼굴만봐도 존잼 <br className="md_br" />물론 기본이 잘생기고 귀엽고 이쁜얼굴임😀😀😀😀
              </p>
              <p>
                <Button as={Link} to="/detail/1">영타기의 된장골드</Button>
              </p>
            </Container>
          </Jumbotron>
          <div className="container">
            <div className="row">
              {
                shoesData.map((shoeData, i) => {
                  return ( 
                    <Card 
                      key={shoeData.id + i}
                      shoeData={shoeData}
                    />
                  );
                })
              }
            </div>
            <Button onClick={() => {

              setShoeLoading(true);
              
              axios.get('https://codingapple1.github.io/shop/data2.json')//get 요청
              .then((result) => {
                console.log('성공');
                console.log(result.data);
                setShoesData([...shoesData, ...result.data]);
                setShoeLoading(false);
              })//성공시
              .catch((error) => {
                console.log('쉴패');
                console.log(error);
                setShoeLoading(false);
              }); //실패시
            }}>
              {
                shoeLoading
                ? <Spinner
                    as="span"
                    animation="border"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                  />
                : null
              }
              더보기
            </Button>
          </div>
        </Route>

        <Route path="/detail/:id">
          <Detail shoes={shoesData} stock={shoeStock} setStock={setShoeStock}/>
        </Route>
        
        <Route path="/:id">
          <div>아무거나아아</div>
        </Route>
      </Switch>

    </div>
  );
}

function Card (props) {
  return (
    <div className="col-md-4">
      <Link to={`/detail/${props.shoeData.id}`} className="">
        <img src={`https://codingapple1.github.io/shop/shoes${props.shoeData.id + 1}.jpg`} alt="상품이미지" width="100%" />
        <h4>{props.shoeData.title}</h4>
        <p>{props.shoeData.content}</p>
        <p>{props.shoeData.price}</p>
      </Link>
    </div>
  );
}

export default App;
